import Qs from './formatURL'

//线上环境
// export const ROOTHTTP = "https://www.czds-oa.com/prod-api" 


//开发环境
export const ROOTHTTP = "http://47.94.221.213:4380/prod-api"

// 写功能
function myPro(options) {
	const csrfToken = uni.getStorageSync('token');
	var head = '';
	var httpUrl = ROOTHTTP
	if (csrfToken) {
		head = `Bearer ${csrfToken}`
		if (options.token != undefined && !options.token) {
			head = ''
		}
	} else {
		head = csrfToken
	};
	/*if (options.advertisement != undefined && options.advertisement) {
		httpUrl = ROOTHTTP
	} else if (options.isOA) {
		httpUrl = OAHTTP;
	} else if (options.isSTUDY) {
		httpUrl = STUDYHTTP;
	} else if (options.isOpen) {
		httpUrl = OPENHTTP;
	} else {
		httpUrl = BASEHTTP
	}*/


	//console.log(httpUrl);
	// 返回一个 promise对象即可
	return new Promise((resolve, reject) => {
		wx.getNetworkType({
			success: res => {
				const networkType = res.networkType;
				if (networkType == "none") {
					wx.showToast({
						title: '当前无网络,请检查网络！',
						icon: 'none', //图标,
						duration: 1500, //延迟时间,
						mask: true, //显示透明蒙层，防止触摸穿透,
						success: res => {}
					})
				} else if (networkType == "2g") {
					wx.showToast({
						title: '您已切换至2g网络,可能导致网络不佳！',
						icon: 'none', //图标,
						duration: 1500, //延迟时间,
						mask: true, //显示透明蒙层，防止触摸穿透,
						success: res => {}
					})
				}
			}
		});
		if (options.method == 'get') {
			if (!options.url.includes('?') && options.data) {
				if (options.noGetData) {
					options.url = options.url;
				} else {
					options.url = options.url + '?' + Qs.stringify(options.data)
				}
				options.data = ''
			}
		}

		// 执行异步操作
		wx.request({
			url: httpUrl + options.url || 'url', //开发者服务器接口地址",
			data: options.data, //请求的参数",
			data1: options.data1 || '', //请求的参数",
			method: options.method || 'GET',
			dataType: options.dataType || 'json', //如果设为json，会尝试对返回的数据做一次 JSON.parse
			timeout: 15000,
			header: {
				'content-type': 'application/json',
				'Authorization': head,
			} || {},
			// success: resolve,
			success: function(res) {
				// uni.hideToast();
				uni.hideLoading()
				if (res.data.code == 200) {
					resolve(res.data)
				} else if (res.data.code == 401) {
					uni.showToast({
						title: '请先登录!',
						icon: "none",
						mask: true
					});

					uni.setStorageSync('token', '')
					uni.redirectTo({
						url: '/pages/subMine/login'
					})
				} else if (res.data.code == 0) {
					resolve(res.data)
				} else {
					uni.showToast({
						title: res.data.msg || '请求失败',
						icon: "none",
						mask: true
					});
					reject(res.data)
				}
			},
			fail: function(err) {
				console.log('请求失败:', err);
				resolve(err)
			}
		});


	})
}

// 导出即可
export default {
	myPro,
	// myPro2,
	ROOTHTTP
};